<template>
  <v-container class="pa-0">
    <!-- Header -->
    <PDHeader></PDHeader>

    <!-- Main -->
    <v-container class="mt-15 px-0">
      <!-- 日曆套件 -->
      <FullCalendar :options="calendarOptions">
      </FullCalendar>

      <!-- 點擊日期彈跳燈箱 -->
      <v-dialog v-model="dialog">
        <v-card>

          <v-card-title>
            <span>新增提醒</span>
            <v-btn icon @click="dialog = false" class="close-btn">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-card-title>

          <v-card-text>
            <span>請選擇標籤</span>
            <v-select :items="tagbox"></v-select>

          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">關閉</v-btn>
            <v-btn color="blue darken-1" @click="saveEvent">儲存</v-btn>
          </v-card-actions>

        </v-card>
      </v-dialog>

    </v-container>

    <!-- Footer -->
    <PDFooter></PDFooter>

  </v-container>
</template>

<script setup>
// Components
import PDHeader from '@/components/PD-Header.vue';
import PDFooter from '@/components/PD-Footer.vue'
import { ref } from 'vue';
import { useStore } from 'vuex';
import { reactive } from 'vue';

//日曆元件
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import timeGridPlugin from '@fullcalendar/timegrid'
import listPlugin from '@fullcalendar/list'
import esLocale from '@fullcalendar/core/locales/zh-tw'

// 假資料
const calendar_event_list = useStore().state.calendar_event_list;
const dialog = ref(false);
const evtStart = ref(new Date());
const tagbox = [1, 2, 3, 4, 5];


//載入日曆設定
const calendarOptions = reactive({
  plugins: [dayGridPlugin, interactionPlugin, timeGridPlugin, listPlugin],
  initialView: 'dayGridMonth',
  headerToolbar: {
    start: 'title', // will normally be on the left. if RTL, will be on the right
    end: 'prev,addbtn,next' // will normally be on the right. if RTL, will be on the left
  },
  contentHeight: 400,
  locale: esLocale,
  editable: true,
  selectable: true,
  weekends: true,
  eventSources: [
    // your event source
    {
      events: calendar_event_list

    }

  ],
  eventClick: function () {
    alert('點擊');
  },
  dateClick: function (info) {
    dialog.value = true;
    evtStart.value = info.date;
  }
})

</script>


<style lang="scss" scoped>
.close-btn {
  width: 40px;
  height: 40px;
  box-shadow: none;
  position: absolute;
  top: 0;
  right: 0;
}
</style>
